.search-block {
    padding: 0 154px;

    @media only screen and (max-width: $bp-xxl){
        padding: 0;
    }

    @media only screen and (max-width: $bp-md){
        background: #333333;
        border-radius: 8px; 
        margin: 20px 0;
        padding: 30px 0 20px;
    }

    &__header {
        margin-bottom: 10px;
        padding-bottom: 32px;

        @media only screen and (max-width: $bp-md){
            padding: 0 20px 32px 30px;
            margin-bottom: 24px;
            border-bottom: 1px solid #4F4F4F;
        }
    }

    &__uppertitle {
        font-size: 36px;
        line-height: 160%;
        margin-bottom: 10px;

        @media only screen and (max-width: $bp-lg){
            font-size: 24px;
        }

        @media only screen and (max-width: $bp-md){
            font-size: 18px;
            margin-bottom: 20px;
        }
    }

    &__title-wrapper {
        display: flex;
        align-items: center;
        text-decoration: none;
    }

    &__title {

        @media only screen and (max-width: $bp-md){
            margin-right: auto;
        }
    }

    &__title-icon {
        margin-left: 24px;
        margin-top: 16px;

        @media only screen and (max-width: $bp-lg){
            width: 40px;
            height: 40px;
            margin-top: 6px;
        }

        @media only screen and (max-width: $bp-md){
            width: 24px;
            height: 24px;
            margin: auto 0 auto 24px;
        }
    }

    &__body {

        @media only screen and (max-width: $bp-md){
            padding: 0 20px;
        }
    }

    &__row {
        margin-right: -22.5px;
        margin-left: -22.5px;
        margin-bottom: -40px;

        @media only screen and (max-width: $bp-md){
            margin-right: -10px;
            margin-left: -10px;
        }
    }

    &__col {
        padding-right: 22.5px;
        padding-left: 22.5px;
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-md){
            padding-right: 10px;
            padding-left: 10px;
        }

        &--button {
            @media only screen and (max-width: $bp-xl){
                order: 2;
            }
        }

        &--link {

            @media only screen and (max-width: $bp-md){
                margin-bottom: 20px;
            }
        }
    }

    &__select-wrapper{
        display: flex;
        align-items: center;
        
        &--checkbox {
            border-bottom: 1px solid #4F4F4F;

            @media only screen and (max-width: $bp-md){
                .SumoSelect{
                    border-bottom: none;
                }
                .select{
                    display: none;
                }
                
                .CaptionCont {
                    display: none;
                }

                .optWrapper{
                    position: static !important;
                    visibility: visible;
                    opacity: 1;
                    height: 100%;
                    background-color: transparent;
                    box-shadow: none !important;
                    border: none !important;
                }

                .options{
                    padding: 0 !important;
                    display: flex !important;
                    height: 100%;
                    background-color: transparent;
                }

                .opt{
                    padding-left: 0 !important;
                    flex: 1 1 auto;
                    justify-content: center;
                    padding: 0 !important;
                    margin-bottom: 0 ;
                    color: $gray-light;
                    font-size: 18px;
                    line-height: 140%;

                    &.selected{
                        border-bottom: 1px solid transparent !important;
                        border-image: $gradient !important;
                        border-image-slice: 1 !important;
                    }

                    span{
                        display: none !important;
                    }
                }
            }
        }
    }

    &__select-label{
        font-size: 24px;
        line-height: 140%;
        padding:0 24px 0 4px; 

        @media only screen and (max-width: $bp-lg){
			font-size: 20px;
		}
	
		@media only screen and (max-width: $bp-md){
			font-size: 18px;
		}
    }

    &__select{

        &--checkbox{

        }
    }

    &__button {
        width: 100%;
        height: 56px;

        @media only screen and (max-width: $bp-md){
            font-size: 18px;
        }
    }

    &__link-wrapper {
        display: flex;
        align-items: center;
        width: 100%;
        padding-top: 40px;
        border-top: 1px solid #4F4F4F;

        @media only screen and (max-width: $bp-xl){
            border-top: none;
            padding-top: 0;
            min-height: 56px;
        }

        @media only screen and (max-width: $bp-md){
            justify-content: center;
        }
    }

    &__link{
        min-height: 40px;
        opacity: 1;
    }
}
