
.slider {
    &--hidden{
        overflow: hidden;
    }

    &__inner{
        overflow: visible;

    }

    &__wrapper {
    }

    &__slide {
        height: auto;
    }

    &__navigation {
        display: flex;
        justify-content: space-between;
        width: 100%;
        pointer-events: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
    }

    &__button {
        pointer-events: all;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100px;
        height: 100px;
        flex-shrink: 0;
        background: $gradient;
        fill: $gray-dark;
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.05), 0px 0.1px 0.3px rgba(0, 0, 0, 0.014), 0px 0.3px 0.8px rgba(0, 0, 0, 0.02), 0px 0.5px 1.5px rgba(0, 0, 0, 0.025), 0px 0.9px 2.7px rgba(0, 0, 0, 0.03), 0px 1.7px 5px rgba(0, 0, 0, 0.036);
        transition: fill 0.25s ease 0.15s, background-color 0.25s ease, opacity 0.25s ease;
        z-index: 1;

        &--prev{
            border-radius: 0 4px 4px 0;
        }

        &--next{
            border-radius: 4px 0 0 4px;
        }

        &.swiper-button-disabled{
            opacity: 0.24;
        }

        &:hover{
            // fill: #fff;
            // background-color: $brown;
        }

        @media only screen and (max-width: $bp-lg){
            width: 40px;
            height: 40px;
        }
    }

    &__button-icon{
        width: 56px;
        height: 56px;

        @media only screen and (max-width: $bp-lg){
            width: 24px;
            height: 24px;
        }
    }

    &__pagination {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 48px;

        color: #fff;

        @media only screen and (max-width: $bp-xl){
            margin-top: 36px;
        }

        @media only screen and (max-width: $bp-lg){
            margin-top: 24px;
        }

        @media only screen and (max-width: $bp-md){
            margin-top: 16px;
        }

        &.swiper-pagination-lock{
            display: none;
        }
    }

    &__pagination-item{
        cursor: pointer;
        display: block;
        width: 10px;
        height: 10px;
        flex-shrink: 0;
        border-radius: 50%;
        margin: 0 4px;
        background-color: #4F4F4F;
        transition: background-color 0.45s ease;

        &.swiper-pagination-bullet-active{
            background-color: $gold;
        }
    }

    &__fraction{
        display: flex;
        align-items: center;
        position: absolute;
        bottom: 40px;
        right: 48px;
        z-index: 3;
        height: 65px;
    }

    &__fraction-item{
        font-family: 'NotoSerifDisplay';
        font-weight: 600;
        font-size: 24px;
        line-height: 33px;
        text-transform: uppercase;
        background: $gradient;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;

        &--current{
            margin: auto 8px 8px 0;
        }

        &--next{
            opacity: 0.4;
            margin: 8px 4px auto 8px;
        }

        &--total{
            font-size: 18px;
            line-height: 25px;
            opacity: 0.4;
            margin-bottom: auto;
        }
    }

    &__fraction-line{
        width: 240px;
        height: 2px;
        background: rgba(218, 190, 131, 0.16);
        border-radius: 8px;
        position: relative;
        overflow: hidden;

        &.is-animate::after{
            animation: fractionLine ease 6s infinite;

        }
        
        &::after{
            content: "";
            width: 100%;
            height: 100%;
            border-radius: 8px;
            background: $gradient;
            position: absolute;
            left: 0;
            top: 0;
            animation: none;
        }
    }
}
