.link{
    display: inline-flex;
    align-items: center;
    text-decoration: none;
    color: #fff;
    fill: #fff;
    opacity: 0.64;
    transition: opacity 0.25s ease;

    &:hover{
        opacity: 1;
    }

    &__icon{
        margin-left: 4px;
        width: 24px;
        height: 24px;
    }

    &--gold{
        color: $gold;
        fill: $gold;
    }

    &--gradient{
        background: $gradient;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;	
        fill: $gradient;
        fill: url(#gradient);
    }
}