@keyframes fractionLine {

	0%{ 
		transform-origin: right;
		transform: scaleX(100%);
	}
	5% {
		transform: scaleX(0%);
		transform-origin: right;
	}
	6%{
		transform: scaleX(0%);
		transform-origin: left;
	}
	100%{
		transform: scaleX(100%);
		transform-origin: left;
	}
}

