.range{
    background-color: transparent;
    font-size: 24px;
    line-height: 140%;

    @media only screen and (max-width: $bp-lg){
        font-size: 20px;
    }

    @media only screen and (max-width: $bp-md){
        font-size: 18px;
    }

    &__label{
        position: absolute;
        padding:0 14px;
        top: 16px;
        color: #9E9E9E;
        
        &:first-of-type{
            left: 0;
        }

        &:last-of-type{
            left: 50%;
        }
    }

    &__inputs{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        color: $gray-medium;
        border-color: $gray-medium;
        background-color: transparent;
    }

    &__input-wrapper{
        margin-right: 20px;
        
        &:last-of-type{
            margin-right: 0;
        }
    }

    &__input{
        display: none;
    }

    &__input-count{
        color: $gray-light;
    }

    &__slider{
        width: 100%;
        margin-top: 6px;
        height: 16px;
    }

    .noUi{
        &-target{
            // padding: 0 9px;
            width: 100%;
            border-radius: 0px;
            border: none;
        }

        &-base{
            display: flex;
            align-items: center;
        }

        &-connects{
            height: 1px;
            // margin: auto -9px;
            // width: calc(100% + 18px);
            background: $gray-light;
            cursor: pointer;
        }

        &-handle{
            width: 9px;
            height: 9px;
            border-radius: 50%;
            background-color: #DABE83;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            top: 4px;
            
            &-lower{
                right: 0;
            }
            &-upper{
                right: -4px;
            }
        }

        &-connect{
            background: $gradient;
        }
    }
}