h1, .h1,
h2, .h2, 
h3, .h3, 
h4, .h4, 
h5, .h5, 
h6, .h6 {
	font-weight: 600;
	letter-spacing: 0.04em;
	text-transform: uppercase;
	font-family: 'NotoSerifDisplay';
	color: $gold;
	-webkit-text-stroke: 0.5px $gold;
	background: $gradient;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	background-clip: text;
	text-fill-color: transparent;	
}

h1, .h1 {
	font-weight: 700;
	font-size: 56px;
	line-height: 76px;

	@media only screen and (max-width: $bp-lg){
		font-size: 36px;
		line-height: 49px;
	}

	@media only screen and (max-width: $bp-md){
		font-size: 24px;
		line-height: 33px;
	}
}

h2, .h2 {
	font-size: 40px;

}


h3, .h3 {
	font-size: 32px;

}


h4, .h4 {
	font-size: 25px;

}

h5, .h5 {
	font-size: 20px;

}



.text {
	font-family: 'Roboto';
	font-size: 20px;
	line-height: 160%;	
	
	
	&-xs {
		font-size: 14px;
		line-height: 140%;
	}

	&-sm{
		font-size: 16px;
		line-height: 140%;
	}

	&-lh{
		line-height: 140%;
	}

	&-xl{
		font-size: 24px;
		line-height: 140%;

		@media only screen and (max-width: $bp-lg){
			font-size: 20px;
		}

		@media only screen and (max-width: $bp-md){
			font-size: 18px;
		}
	}

	&-regular {
		font-weight: 400 !important;
	}

	&-medium {
		font-weight: 500 !important;
	}

	&-semibold {
		font-weight: 600 !important;
	}
	
	&-bold {
		font-weight: 700 !important;
	}



	&-uppercase {
		text-transform: uppercase;
	}

	&-align--center {
		text-align: center;
	}
}

p {
	font-weight: 500;
	font-size: 16px;
	line-height: 25px;
	
	&:last-of-type {
		margin-bottom: 0;
	}
}
