.SumoSelect  {
	height: 56px;
	border-bottom: 1px solid transparent;
    border-image: $gradient;
    border-image-slice: 1;
	border-radius: 4px;
	width: 100%;
    font-size: 24px;
    line-height: 140%;
	transition: border-color 0.3s ease;

    @media only screen and (max-width: $bp-lg){
        font-size: 20px;
    }

    @media only screen and (max-width: $bp-md){
        font-size: 18px;
    }

	&.open {
		z-index: 5;

        .optWrapper{
            display: block;
            visibility: visible;
            opacity: 1;
        }
	}

	&:hover {
		@media (hover: hover) {
			border-color: $gray-dark;
		}
	}

	&.SelectBox {
		position: relative;
		z-index: 4;
	}

	.select {
		height: 100%;
		border: none !important;
		padding: 0;
		color: $gray-dark;
		display: flex;
		align-items: center;
		border-radius: 4px;
		padding-left: 4px;
		cursor: pointer;
		box-shadow: none !important;

		&:before {
			content: '';
			position: absolute;
			top: 50%;
			right: 0;
			width: 24px;
			height: 24px;
			z-index: 4;
            transform: translateY(-50%);
			background-repeat: no-repeat;
			background-position: center center;
			background-image: url("data:image/svg+xml,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.42279 8.46967C5.68906 8.2034 6.10573 8.1792 6.39934 8.39705L6.48346 8.46967L12.9531 14.939L19.4228 8.46967C19.6891 8.2034 20.1057 8.1792 20.3993 8.39705L20.4835 8.46967C20.7497 8.73594 20.7739 9.1526 20.5561 9.44621L20.4835 9.53033L13.4835 16.5303C13.2172 16.7966 12.8005 16.8208 12.5069 16.6029L12.4228 16.5303L5.42279 9.53033C5.1299 9.23744 5.1299 8.76256 5.42279 8.46967Z' fill='url(%23paint0_linear_3175_23925)'/%3E%3Cdefs%3E%3ClinearGradient id='paint0_linear_3175_23925' x1='-0.943041' y1='4.7336' x2='32.2105' y2='17.3976' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%23DABE83'/%3E%3Cstop offset='1' stop-color='%23C9792F'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E%0A");
			transition: transform 0.3s ease;
		}


		& > label {
			display: none;
		}
	}

	.select > span,
	.placeholder {
		position: relative;
		visibility: visible !important;
		z-index: 4;
		cursor: pointer;
		padding-right: 40px;
		font-style: normal !important;
		color: $gray-light;
		font-size: 24px;
		line-height: 140%;
	
		@media only screen and (max-width: $bp-lg){
			font-size: 20px;
		}
	
		@media only screen and (max-width: $bp-md){
			font-size: 18px;
		}
	
	}

	.search-txt {
		border: 1px solid #DBDBDC !important;
		height: 48px;
		top: 100% !important;
		left: 16px !important;
		width: calc(100% - 32px) !important;
		z-index: 4;
		background-repeat: no-repeat;
		background-position: right 16px center;
		background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='9.375' cy='9.06812' r='6.875' stroke='%2386868B' stroke-width='1.5'/%3E%3Cline x1='14.8107' y1='14.6931' x2='17.5' y2='17.3825' stroke='%2386868B' stroke-width='1.5' stroke-linecap='round'/%3E%3C/svg%3E%0A");
		padding-right: 40px !important;
		font-size: 14px;
        display: none !important;

	}

	.select--basic  +  .optWrapper {
		.opt:first-of-type {
			display: none;
		}
	}

	.optWrapper {
        position: absolute !important;
        top: 100% !important;
        left: 0 !important;
        right: auto !important;
        bottom: auto !important;
        margin: 0 !important;
        padding-bottom: 0 !important;
        max-height: none !important;
		background: #fff;
        width: 100% !important;
        display: block;
        visibility: hidden;
        opacity: 0;
		z-index: 3;
		box-shadow: none;
		border: 1px solid rgba(218, 190, 131, 0.3) !important;
		border-radius: 0px;
        box-shadow: 0px 4px 16px rgba(218, 190, 131, 0.16);
        transition: visibility 0.25s ease, opacity 0.25s ease;

		.options {
            padding: 24px;
            box-sizing: border-box;
            max-height: 298px;
            scrollbar-color: $gold  #F5F5F7;
            scrollbar-width: thin;

            &::-webkit-scrollbar {
                width: 2px;
            }
            &::-webkit-scrollbar-track {
                background-color: #F5F5F7;
            }
            &::-webkit-scrollbar-thumb {
                background-color: $gold;
                border-radius: 40px;
            }
		}
	}

    &.open {
		border-color: transparent;

		.select::before {
            background-image: url("data:image/svg+xml,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.73054 20.2784C4.54301 20.0909 4.43765 19.8365 4.43765 19.5713C4.43765 19.3061 4.54301 19.0517 4.73054 18.8642L18.8727 4.72205C19.0602 4.53451 19.3166 4.42711 19.5818 4.42711C19.847 4.42711 20.0994 4.53451 20.2869 4.72205C20.4744 4.90958 20.5818 5.16189 20.5818 5.42711C20.5818 5.69232 20.4744 5.94872 20.2869 6.13626L6.14476 20.2784C5.95722 20.4659 5.70287 20.5713 5.43765 20.5713C5.17243 20.5713 4.91808 20.4659 4.73054 20.2784Z' fill='url(%23paint0_linear_1210_5725)'/%3E%3Cpath d='M20.2891 20.2782C20.1016 20.4658 19.8472 20.5711 19.582 20.5711C19.3168 20.5711 19.0625 20.4658 18.8749 20.2782L4.73279 6.13611C4.54525 5.94857 4.43785 5.69217 4.43785 5.42696C4.43785 5.16174 4.54525 4.90943 4.73279 4.7219C4.92033 4.53436 5.17263 4.42696 5.43785 4.42696C5.70307 4.42696 5.95947 4.53436 6.147 4.7219L20.2891 18.864C20.4767 19.0516 20.582 19.3059 20.582 19.5711C20.582 19.8364 20.4767 20.0907 20.2891 20.2782Z' fill='url(%23paint1_linear_1210_5725)'/%3E%3Cdefs%3E%3ClinearGradient id='paint0_linear_1210_5725' x1='-2.73012' y1='25.1547' x2='17.2381' y2='33.0364' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%23DABE83'/%3E%3Cstop offset='1' stop-color='%23C9792F'/%3E%3C/linearGradient%3E%3ClinearGradient id='paint1_linear_1210_5725' x1='25.1655' y1='27.7389' x2='33.0471' y2='7.77069' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%23DABE83'/%3E%3Cstop offset='1' stop-color='%23C9792F'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E%0A");
        }
	}
	& > .optWrapper.multiple > .options li.opt span,
	.select-all > span{
		display: flex;
	}

	& > .optWrapper > .options li.opt{
		padding: 0;
	}
	& > .optWrapper.multiple > .options li.opt{
		padding-left: 24px;
	}
	.opt {
		display: flex;
		align-items: center;
        // padding: 0 !important;
		min-height: 22px;
		border: none !important;
		transition: background-color 0.3s ease, color 0.3s ease;
		font-size: 16px;
        line-height: 140%;
		font-weight: 400;
        color: #4F4F4F;
        margin-bottom: 16px;

        &:last-of-type{
            margin-bottom: 0;
        }

		&:hover {
			background-color: transparent !important;
			color: $gold;
			cursor: pointer;

			span {
				border-color: $gold !important;
			}
		}

		&.hidden {
			display: none;
		}

        &.selected{
			background: $gradient;
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
			background-clip: text;
			text-fill-color: transparent;	
        }

	}

	.select--multiple +  .optWrapper {
		.opt {
			span {
				display: flex;
				align-items: center;
				justify-content: center;
				margin-left: 0 !important;
				width: 16px !important;
				height: 16px !important;
                left: 0;
                top: 50% !important;
                bottom: auto !important;
				background: #FFFFFF;
                background-position: center;
                background-repeat: no-repeat;
                background-image: url("data:image/svg+xml,%3Csvg width='17' height='16' viewBox='0 0 17 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='1.45312' y='0.5' width='15' height='15' rx='1.5' stroke='url(%23paint0_linear_1210_3602)'/%3E%3Cdefs%3E%3ClinearGradient id='paint0_linear_1210_3602' x1='-5.3913' y1='-6.6191' x2='32.1767' y2='1.25036' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%23DABE83'/%3E%3Cstop offset='1' stop-color='%23C9792F'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E%0A");
				border-radius: 2px;
                transform: translateY(-50%);
                overflow: hidden;

				&:before {
					content: '';
					display: block;
					opacity: 0;
					width: 10px;
					height: 8px;
                    opacity: 0;
					background-position: center;
					background-repeat: no-repeat;
					background-image: url("data:image/svg+xml,%3Csvg width='11' height='9' viewBox='0 0 11 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.86621 4.03516H1.86639C1.94569 4.03525 2.02393 4.05331 2.09526 4.08798C2.16653 4.12262 2.22904 4.17295 2.27809 4.2352L1.86621 4.03516ZM1.86621 4.03516H1.04706C0.843433 4.03516 0.729727 4.26975 0.855446 4.4292L0.855496 4.42927L4.06526 8.49567L4.06531 8.49573C4.2756 8.76188 4.67881 8.76114 4.8898 8.49617L4.89021 8.49566L10.6128 1.24398C10.613 1.24377 10.6131 1.24357 10.6133 1.24336C10.7415 1.08293 10.623 0.85 10.4221 0.85H9.60292C9.44253 0.85 9.2898 0.923501 9.19072 1.05044C9.19063 1.05055 9.19055 1.05066 9.19046 1.05077L4.47716 7.02157M1.86621 4.03516L4.47716 7.02157M4.47716 7.02157L2.27818 4.23531L4.47716 7.02157Z' fill='white' stroke='white' stroke-width='0.3'/%3E%3C/svg%3E%0A");
                    transition: opacity 0.35s ease;
				}

				i {
					display: none;
				}
			}

			&.selected {
				span {
					background: $gradient;

					&:before {
						opacity: 1;
					}
				}
			}
		}
	}

	&[is-selected="true"]  {
		.reset-all {
			color: $black;
		}
	}

	.MultiControls {
		display: none !important;
		border: none !important;
		text-align: right;

		.btnCancel {
			display: none !important;
		}

		.btnOk {
			background-color: $gold;
			color: #fff;
			border: none !important;
			transition: background-color 0.3s ease;
			height: 40px;
			padding-top: 10px !important;
			padding-bottom: 0 !important;
			border-radius: 0px 0px 4px 0px !important;
			font-size: 14px;

			&:hover {
				background-color: $gold !important;
			}
		}
	}

	.reset-all {
		position: absolute;
		width: 50%;
		left: 0;
		bottom: 0;
		border: none !important;
		z-index: 20;
		pointer-events: auto !important;
		height: 40px;
		padding: 10px 0 0;
		text-align: center;
		font-size: 14px;
		transition: background-color 0.3s ease;
        display: none;

		&:hover {
			background-color: $gold !important;
			color: #fff !important;
		}
	}

	.no-match {
		display: none !important;
	}

    & > .CaptionCont{
        background-color: transparent;
    }

}

