.icon {
    flex-shrink: 0;
    
    &--sm{
        width: 24px;
        height: 24px;
    }

    &--md{
        width: 32px;
        height: 32px;
    }

    &--lg{
        width: 48px;
        height: 48px;
    }

    &--xl{
        width: 56px;
        height: 56px;
    }

    &--gradient{
        fill: url(#gradient) !important;
    }
}
