.intro-block {
    position: relative;

    &__slider {
        height: 100vh;
        margin: 0 -40px;

        @media only screen and (max-width: $bp-md){
            margin: 0 -20px;
        }

        .slider {
            &__inner {
                height: 100%;
            }

            &__pagination {
                position: absolute;
                bottom: 88px;
                left: 0;
                width: 100%;
                z-index: 3;
            }
        }
    }

    &__item {
        display: flex;
        align-items: center;
        height: 100%;
        position: relative;

        @media only screen and (max-width: $bp-md){
            align-items: flex-end;
            padding: 0 20px;
        }

        &::after{
            pointer-events: none;
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: url('../img/noise.png') 584px 328px repeat, linear-gradient(90deg, #292929 -33.05%, rgba(41, 41, 41, 0.9) 25.8%, rgba(41, 41, 41, 0) 50%);
            background-blend-mode: screen, normal;

            @media only screen and (max-width: $bp-xxl){
                background: url('../img/noise.png') 584px 328px repeat, linear-gradient(90deg, #292929 -33.05%, rgba(41, 41, 41, 0.9) 18.8%, rgba(41, 41, 41, 0) 50%);
            }

            @media only screen and (max-width: $bp-xl){
                background: url('../img/noise.png') 584px 328px repeat, linear-gradient(90deg, #292929 -33.05%, rgba(41, 41, 41, 0.9) 25.8%, rgba(41, 41, 41, 0) 50%);
            }

            @media only screen and (max-width: $bp-md){
                background: url('../img/noise.png') 584px 328px repeat, linear-gradient(360deg, #292929 0%, rgba(41, 41, 41, 0.9) 35.35%, rgba(41, 41, 41, 0) 56.09%);
            }
        }
    }

    &__item-content {
        position: relative;
        z-index: 2;
        color: $gray-light;
        max-width: 532px;
        margin-left: 272px;

        @media only screen and (max-width: $bp-lg){
            margin-left: 70px;
        }

        @media only screen and (max-width: $bp-md){
            margin-left: 0;
            margin-bottom: 98px;
            padding-bottom: 20px;
        }
    }

    &__item-title {

        @media only screen and (max-width: $bp-md){
            margin-bottom: 8px;
        }
    }

    &__item-subtitle {
        color: $gold;
        margin-bottom: 56px;

        @media only screen and (max-width: $bp-md){
            margin-bottom: 20px;
        }

        &::before{
            content: "";
            display: inline;
            margin-right: 8px;
            font-weight: 400;
            font-size: 24px;
            line-height: 140%;
        }
    }

    &__item-text {
        padding-left: 24px;
        margin-bottom: 56px;

        @media only screen and (max-width: $bp-lg){
            padding-left: 22px;
        }

        @media only screen and (max-width: $bp-md){
            margin-bottom: 40px;
            padding-left: 20px;
        }
    }

    &__item-link {
        height: 40px;
        margin-left: 24px;

        @media only screen and (max-width: $bp-lg){
            margin-left: 22px;
        }

        @media only screen and (max-width: $bp-md){
            margin-left: 20px;
            opacity: 0.32;
        }
    }

    &__item-img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: cover;
    }

    &__link-scroll {
        position: absolute;
        bottom: 48px;
        left: 40px;
        z-index: 2;

        @media only screen and (max-width: $bp-lg){
            left: 94px;
            bottom: 24px;
        }

        @media only screen and (max-width: $bp-md){
            left: 50%;
            bottom: 20px;
            transform: translateX(-50%);
        }
    }
}
