@font-face {
	font-family: 'Roboto';
	src: url('../fonts/Roboto-Regular.eot');
	src: local('Roboto Regular'), local('Roboto-Regular'),
		url('../fonts/Roboto-Regular.eot?#iefix') format('embedded-opentype'),
		// url('../fonts/Roboto-Regular.woff2') format('woff2'),
		// url('../fonts/Roboto-Regular.woff') format('woff'),
		url('../fonts/Roboto-Regular.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}
//====================

@font-face {
	font-family: 'NotoSerifDisplay';
	src: local('../fonts/NotoSerifDisplay SemiBold'),
	local('NotoSerifDisplay-Semibold'),
		url('../fonts/NotoSerifDisplay-SemiBold.otf') format('otf'),
		// url('../fonts/NotoSerifDisplay-SemiBold.woff2') format('woff2'),
		// url('../fonts/NotoSerifDisplay-SemiBold.woff') format('woff'),
		url('../fonts/NotoSerifDisplay-SemiBold.ttf') format('truetype');
	font-weight: 600;
	font-style: normal;
}
@font-face {
	font-family: 'NotoSerifDisplay';
	src: local('../fonts/NotoSerifDisplay Bold'),
	local('NotoSerifDisplay-Bold'),
		url('../fonts/NotoSerifDisplay-Bold.otf') format('otf'),
		// url('../fonts/NotoSerifDisplay-SemiBold.woff2') format('woff2'),
		// url('../fonts/NotoSerifDisplay-SemiBold.woff') format('woff'),
		url('../fonts/NotoSerifDisplay-Bold.ttf') format('truetype');
	font-weight: 700;
	font-style: normal;
}